@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.header {
    transition: all 0.3s ease;
    background-color: white;
    height: 11vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    box-shadow: 0 4px 5px 0 rgb(223, 222, 222);
}


.cels-fahr-selector {
    display: flex;
    width: 100px;
    height: 28px;
    cursor: pointer;
    border: 1px solid #609cb1;
}

.temp-item {
    font-size: 20px;
}

.cels-block {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 100%;
    background-color: #609cb1;
    color: white;
    transition: all 0.3s ease;
}

.cels-block-after {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 100%;
    background-color: white;
    transition: all 0.3s ease;
}

.fahr-block {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 100%;
    background-color:  #609cb1;
    color: white;
    transition: all 0.3s ease;
}

.fahr-block-after {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 100%;
    background-color: white;
    transition: all 0.3s ease;
}

.cities-list-before {
   display: none;
}

.cities-list-after {
    width: 263px;
    height: 300px;
    overflow: auto;
    z-index: 20;
    background-color:rgb(246, 246, 251); 
    box-shadow : 0px 8px 16px 0px rgba(0,0,0,0.2);
    position: absolute;
    outline: none;
}

.search-item {
    list-style-type: none;
    display: flex;
    align-items: center;
    background-color: rgb(246, 246, 251);
    height: 35px;
    padding: 3px;
    font-size: 19px;
    padding-left: 7px;
    cursor: pointer;
    outline: none;
}

.search-item:hover {
    background-color: rgb(222, 222, 233);
}

.header-main {
    display: flex;
    align-items: center;
}


.page-name {
    margin: 0px;
    font-family: 'Montserrat', sans-serif;
    font-size: 35px;
    color: rgb(120, 120, 195);
}

span {
    font-size: 17px;
}

.search-input {
    height: 35px;
    font-size: 19px;
    outline: none;
    width: 250px;
    padding-left: 7px;
    margin-right: 10px;
    border: 2px solid rgb(189, 189, 226);
    transition: all 0.3s ease;
}

.search-input:focus {
    border: 2px solid rgb(120, 120, 195);
}

.search-btn {
    height: 41px;
    width: 120px;
    font-size: 16px;
    cursor: pointer;
    background-color: white;
    border: 1px solid rgb(94, 129, 94);
    outline: none;
    color: rgb(94, 129, 94);
    transition: all 0.3s ease;
}

.search-btn:hover {
    color: white;
    background-color: rgb(94, 129, 94);
}
.display {
    
    width: 50%;
    max-height: 89vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.display-none {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 89vh;
    z-index: -5;
    background-color: white;
}


.display-wrapper {
    height: 460px;
    width: 500px;
    box-shadow: #7a797959 0px 5px 15px;
}

.upd-btn-wrapper {
    height: 50px;
    display: flex;
    justify-content: flex-end;
}

.update-btn {
    background-color: white;
    outline: none;
    border: none;
    padding: 15px;
}

.display-main {
    width: 500px;
    height: 410px;
    display: flex;
    justify-content: center;
   
}

.display-after {
    position: absolute;
    width: 50%;
    height: 88vh;
    background-color: white;
    top: 12vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 28px;
    color: rgb(158, 93, 93);
}

.main {
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.main-temp {
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.display-before {
    display: none;
}

.city-name {
    font-size: 30px;
    margin-bottom: 0px;
    margin-top: 80px;
}

.current-date {
    font-size: 21px;
    margin-bottom: 5px;
}

.current-time {
    color: rgb(219, 111, 61);
    font-size: 18px;
    margin-bottom: 5px;
}

.weather-desc {
    font-size: 20px;
    margin-bottom: 0px;
}

.weather-icon {
    width: 80px;
    height: 80px;
}

.temp-block {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.fa-sync-alt {
    color: gray;
    transition: all 1s ease;
}

.fa-sync-alt:hover {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}

.temp {
    font-size: 55px;
}

.feelslike-temp-wrapper {
    font-size: 22px;
}

.temp-block-more {
    font-size: 18px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.humidity {
    margin-bottom: 0px;
}

.celcius-switcher {
    color: black;
    background-color: white;
    outline: none;
    border: none;
    padding: 10px;
    font-size: 25px;
    border-right: 1px solid black;
}

.celcius-switcher-after {
    outline: none;
    border: none;
    padding: 10px;
    font-size: 19px;
    background-color: white;
    color: rgb(90, 87, 87);
    border-right: 1px solid black;
}

.fahrenheit-switcher {
    color: black;
    outline: none;
    border: none;
    padding: 10px;
    font-size: 25px;
    background-color: white;
}

.fahrenheit-switcher-after {
    outline: none;
    border: none;
    padding: 10px;
    font-size: 19px;
    background-color: white;
    color: rgb(90, 87, 87);
}
.list {
    width: 50%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.list-none {
    width: 0;
    overflow: hidden;
}

.radio-text {
    margin-left: 90px;
    font-size: 18px;
    display: flex;
    align-items: center;
}

label {
    cursor: pointer;
}

.radio {
    margin-right: 10px;
    height: 17px;
    width: 17px;
    margin-bottom: 4px;
    cursor: pointer;
}

.settings-wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
    margin-top: 35px;
}

.settings-block {
    opacity: 1;
    border-radius: 0px;
    width: 400px;
    height: 200px;
    margin-bottom: 15px;
    margin-top: 15px;
    background-color: rgb(233, 238, 227);
    -webkit-animation: appear 0.3s;
    animation:         appear 0.3s; 
}

.settings-label {
    text-align: center;
    font-size: 20px;
    margin-bottom: 20px;
}

.label {
    font-size: 33px;
    margin: 0px;
    color: rgb(118, 123, 131);
    margin-right: 15px;

}

.list-item {
    background-color: rgb(232, 242, 250);
    height: 110px;
    margin-bottom: 40px;
    width: 500px;
    border-radius: 0px;
    display: flex;
    flex-direction: column;
    box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px;
    outline: none;
}

button {
    cursor: pointer;
}

.settings-block-none {
    -webkit-animation: disappear 0.3s;
    animation:         disappear 0.3s;
    overflow: hidden;
    height: 0;
    opacity: 0;
    margin-top: 0px;
    border-radius: 0px;
    width: 400px;
    background-color: rgb(233, 238, 227);
}

@-webkit-keyframes disappear {
    0%  {   
        height: 200px;
        opacity: 1;  
        margin-top: 15px;   
        }
    100% { 
            height: 0;
            opacity: 0;
            margin-top: 0px;
         }
  }
  @keyframes disappear {
    0%  {   
        height: 200px;
        opacity: 1;  
        margin-top: 15px;
    }
    100% { 
        height: 0;
        opacity: 0;
        margin-top: 0px;
     }
  }

@-webkit-keyframes appear {
    0%  { 
            opacity: 0; 
            height: 0;
            margin-top: 0px;
        }
    100%{ 
            opacity: 1;
            height: 200px;
            margin-top: 15px;
        }
  }
  @keyframes appear {
    0%  { 
        opacity: 0; 
        height: 0;
        margin-top: 0px;
    }
    100%{ 
        opacity: 1;
        height: 200px;
        margin-top: 15px;
    }   
  }

  @-webkit-keyframes listappear {
    0%   { opacity: 0; }
    100% { opacity: 1; }
  }
  @keyframes listappear {
    0%   { opacity: 0; }
    100% { opacity: 1; }
  }

.settings {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
    margin-top: 20px;
}

.update-settigns { 
    margin-top: 3px;
    height: 38px;
    width: 38px;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    border: none;
    transition: all 1s ease;
    position: relative;
}
.fas {
    font-size: 30px;
    color: rgb(118, 123, 131);
}

.update-settigns:hover {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); 
}

.del-btn-block {
    height: 100%;
    border-radius: 0px;
    display: flex;
    justify-content: flex-end;
    width: 10%;
}

.delete-card {
    background-color: rgb(232, 242, 250);
    border: none;
    border-radius: 0px;
    outline: none;
    height: 35px;
}

.fa-times {
    font-size: 26px;
    color: rgb(206, 119, 119);
    transition: all 0.3s ease;
}

.fa-times:hover {
    color: rgb(184, 89, 89);
}

.main-listitem-block {
    text-align: center;
    height: 100%;
    width: 50%;
    border-radius: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.card-name {
    margin: 0px;
    font-size: 25px;
}

.card-temp {
    margin: 0px;
    font-size: 40px;
}

.upd-info {
    margin: 0px;
    color: rgb(75, 75, 75);
    font-size: 15px;
}

.listitem-description {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 40%;
    border-radius: 0px;
}

.description-weather {
    font-size: 18px;
    margin: 0px;
}

.list-item-main {
    display: flex;
    height: 70%;
    border-radius: 0px;
}

.more-info {
    height: 30%;
    font-size: 17px;
    border-radius: 0px;
    border: none;
    outline: none;
    background-color:rgb(142, 142, 197);
    transition: all 0.3s ease;
    color: white;
}

.more-info:hover {
    background-color: rgb(118, 118, 189);
}

.item-enter {
    opacity: 0;
}

.item-enter-active {
    opacity: 1;
    transition: opacity 300ms ease-out;
}

.item-exit {
    opacity: 1;
}

.item-exit-active {
    opacity: 0;
    transition: opacity 300ms ease-out;
}
.footer {
    height: 130px;
    background-color: rgb(109, 109, 165);
