    font-family: 'Noto Sans JP', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.footer-link {
    margin: 0;
    text-align: center;
    padding-top: 30px;
    font-size: 19px;
}

.profile-link {
    cursor: pointer;
