}

.icons {
    margin-top: 22px;
    padding-bottom: 25px;
    display: flex;
    justify-content: space-evenly;
    width: 200px;
}

.fab, .far {
    font-size: 23px;
    color: rgb(246, 236, 236);
    transition: all 0.4s ease;
    cursor: pointer;
}

.fab:hover {
    color: white;
}

.far:hover {
    color: white;
}
